.lcars-input ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.lcars-input li {
    display: block;
    padding: 9px;
    border-radius: 3px;
    border: 1px solid rgba(244,176,4,0.75);
}

.lcars-input li > label {
    display: block;
    float: left;
    margin-top: -22px;
    background: rgb(15,46,83);
    height: 14px;
    padding: 2px 5px 2px 5px;
    color: rgba(244,176,4,0.75);
    font-size: 14px;
    overflow: hidden;
}

.lcars-input input[type="text"],
.lcars-input input[type="date"],
.lcars-input input[type="datetime"],
.lcars-input input[type="email"],
.lcars-input input[type="number"],
.lcars-input input[type="search"],
.lcars-input input[type="time"],
.lcars-input input[type="url"],
.lcars-input input[type="password"],
.lcars-input textarea,
.lcars-input select {
    -webkit-transition: all 0.30s ease-in-out;
    -moz-transition: all 0.30s ease-in-out;
    -ms-transition: all 0.30s ease-in-out;
    -o-transition: all 0.30s ease-in-out;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    width: 100%;
    display: block;
    outline: none;
    border: 1px solid rgba(244,176,4,0.3);
    border-radius: 3px;
    height: 25px;
    line-height: 25px;
    font-size: 16px;
    color: rgba(244,176,4,0.75);
    margin-top: 3px;
    padding-left: 5px;
    margin-bottom: 8px;
    background-color: rgba(244,176,4,0.15);
}

.lcars-input input[type="text"]:focus,
.lcars-input input[type="date"]:focus,
.lcars-input input[type="datetime"]:focus,
.lcars-input input[type="email"]:focus,
.lcars-input input[type="number"]:focus,
.lcars-input input[type="search"]:focus,
.lcars-input input[type="time"]:focus,
.lcars-input input[type="url"]:focus,
.lcars-input input[type="password"]:focus,
.lcars-input textarea:focus,
.lcars-input select:focus {
    border-color: rgba(244,176,4,0.5);
}

.lcars-input li > span {
    background: rgb(15,46,83);
    display: block;
    padding: 3px;
    margin: 0 -9px -9px -9px;
    text-align: center;
    color: rgb(15,46,83);
    font-family: Arial, Helvetica, sans-serif;
    font-size: 11px;
    border-top: 1px solid rgba(244,176,4,0.3);
    background: rgba(244,176,4,0.75);
}

div.radio-tag input[type="radio"] {
    display: none;
}


div.radio-tag input[type="radio"]:checked+label {
    background-color: rgba(244,176,4,0.7);
    color: rgb(15,46,83);
    border: 1px solid rgba(244,176,4,0.5);
    border-radius: 3px;
    cursor: pointer;
    padding-left: 3px;
    padding-right: 3px;
    padding-bottom: 1px;
}

div.radio-tag label {
    cursor: pointer;
    padding-left: 3px;
    padding-right: 3px;
    padding-bottom: 1px;
    border: 1px solid rgba(244,176,4,0.5);
    border-radius: 3px;
    color: rgba(244,176,4,0.7);
    font-size: smaller;
}

